import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

//! as we DON'T NEED state = no need for class comp. = can just use function comp.
function Movie({ title, summary, poster, year, genres }) { 
  //! from App.js -> <div class="movies">, u passed on genres prop, then w/Movie.PropTypes = {...}, u can typecheck
  //! no need to have genres = as a parameter inside this Movie function, but bcoz it's inside Movie.PropTypes and it's req, so inside App.js = u do need to pass on a prop called gneres no matter what, but just know that u dont need to use inside this Movie function 
  return (
    <div className="movie">
      <img src={poster} alt={title} title={title} />
      <div className="movie__data">
        <h3 className="movie__title" /*style={{backgroundColor: "blue", color: "yellow"}}*/>{title}</h3>
        <h5 className="movie__year">{year}</h5>
        <ul className="movie__genres">
          {genres.map((genre, index) => (
            <li key={index} className="genres__genre">{genre}</li>
          ))}
        </ul>
        <p className="movie__summary">{summary.slice(0, 180)}...</p>
      </div>
    </div>
  )
}

Movie.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  /*medium_cover_image*/ poster: PropTypes.string.isRequired,
  year: PropTypes.number.isRequired,
  genres: PropTypes.arrayOf(PropTypes.string).isRequired
}

export default Movie;